.container {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f5f7fa;
  color: #333333;
  max-width: 680px;
  margin: 0 auto;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
}

/* –ù–æ–≤–æ–µ –ø—Ä–∞–≤–∏–ª–æ –¥–ª—è –æ–±—â–µ–≥–æ —Ä—è–¥–∞ */
.row {
  display: grid;
  grid-template-columns: 90% 10%;
  gap: 1px; /* –Ω–µ–º–Ω–æ–≥–æ –ø—Ä–æ—Å—Ç—Ä–∞–Ω—Å—Ç–≤–∞ –º–µ–∂–¥—É —ç–ª–µ–º–µ–Ω—Ç–∞–º–∏ */
  align-items: center; /* –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–µ –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ */
}

.title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 16px;
  color: #2c3e50;
  text-align: center;
}

.completed {
  background-color: #c8f7c5;  /* –º—è–≥–∫–∏–π –∑–µ–ª—ë–Ω—ã–π —Ñ–æ–Ω */
  border-left: 6px solid #3cb371;
  transition: background-color 0.3s ease;
}


.sentence {
  font-size: 22px;
  line-height: 1.4;
  margin-bottom: 24px;
  padding: 12px 16px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: inset 0 0 5px #d1d9e6;
}

.recognizedWords {
  font-size: 16px;
  color: #5d6d7e;
  margin-bottom: 20px;
  min-height: 40px;
  border-radius: 8px;
  background-color: #e9eff6;
  padding: 10px 14px;
}

.treeGrowth {
  font-size: 18px;
  font-weight: 500;
  color: #27ae60;
  margin-bottom: 30px;
  text-align: center;
}

 
.button {
  background-color: #cccecf;
  border: none;
  padding: 10px;
  border-radius: 50px;
  cursor: pointer;
  text-align: center;
  transition: background-color 0.3s ease;
  margin-left: 10px;
}

.button:hover {
  background-color: #1e8e45;
}

.button:active {
  background-color: #176632;
}

.textContainer {
  font-family: Arial, sans-serif;
  font-size: 20px;
  line-height: 1.8;
  padding-left: 5px;
}

.word {
  color: black;
}

.highlightedWord {
  color: rgb(134, 153, 249);
  /* font-weight: bold; */
  /* border-radius: 10px; */
  background-color: #bce4f1;
  padding: 1x 5px 1px 5px;
}



@media (max-width: 480px) {
  .container {
    padding: 12px;
  }

  .sentence {
    font-size: 18px;
  }

  .title {
    font-size: 22px;
  }

  .row {
  display: grid;
  grid-template-columns: 70% 15% 15%;
  /* gap: 1px; –Ω–µ–º–Ω–æ–≥–æ –ø—Ä–æ—Å—Ç—Ä–∞–Ω—Å—Ç–≤–∞ –º–µ–∂–¥—É —ç–ª–µ–º–µ–Ω—Ç–∞–º–∏ */
  align-items: center; /* –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–µ –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ */
}



  .button {
  margin-left: 0;
    width: 50%;
    padding: 7px;
    text-align: center;
  }
}

.buttons {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-bottom: 16px;
}

button {
  /* –Ω–∞ –≤—Å—è–∫–∏–π —Å–ª—É—á–∞–π, —á—Ç–æ–±—ã –≤—Å–µ –∫–Ω–æ–ø–∫–∏, –∫–æ—Ç–æ—Ä—ã–µ –Ω–µ –≤ —Å—Ç–∏–ª–µ button –Ω–µ –ø–µ—Ä–µ–∫—Ä—ã–≤–∞–ª–∏—Å—å */
  all: unset;
}

button:hover, button:focus {
  outline: none;
}

button:active {
  transform: scale(0.98);
}


