.container {
  padding: 1px;
}

.buttons {
  margin: 10px 0;
}

.liveText {
  margin-top: 20px;
  font-size: 1rem;
  color: #666;
  background: #f3f3f3;
  padding: 10px;
  border-radius: 8px;
}
